html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

ol[class],
ul[class] {
  list-style: none;
}

a {
  text-decoration: none;
}

button,
a {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

button,
input,
select,
textarea {
  color: inherit;
  border: 0;
}

input,
textarea {
  border: none;
  border-radius: 0;
  outline: none;
}

.btn {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fffff0;
  align-items: center;
  background-color: #363838;
  border-radius: 21px;
  border: 1px #363838 solid;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn:hover {
  background-color: #fffff0;
  color: #363838;
  color: #363838;
}
.btn:active {
  transform: scale(0.96);
}

.btn-tag {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #8F8D8D;
  align-items: center;
  background-color: transparent;
  border-radius: 21px;
  border: 1px solid #8f8d8d;
  transition: background-color, color, 0.5s ease-out;
  user-select: none;
}
.btn-tag:hover {
  background-color: #FFEF35;
  color: #363838;
  border-color: #FFEF35;
}
.btn-tag:active {
  transform: scale(0.96);
}

.play {
  width: 80px;
  height: 80px;
  background-color: #979797;
  border-radius: 50px;
  transition: all 0.3s;
}
.play__go {
  width: 0;
  height: 0;
  border-top: 18px solid transparent;
  border-bottom: 18px solid transparent;
  border-left: 28px solid rgba(255, 255, 255, 0.9294117647);
  margin: 22px auto;
  display: block;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.8509803922);
}
.play:active {
  transform: scale(0.96);
}

.input {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #a1a1a1;
  align-items: left;
  border: 1px solid #dfdfdf;
  border-radius: 20px;
  transition: background-color, color, 0.5s ease-out;
}
.input:hover {
  color: #363838;
}
.input:focus {
  border-color: #363838;
  color: #363838;
}

.title {
  color: #363838;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 110px;
  line-height: 134px;
  letter-spacing: 0px;
}

.title-sub {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 80px;
  letter-spacing: 0px;
  color: #363838;
}

.title-block {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 51px;
  letter-spacing: 0px;
  color: #363838;
}

.text {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: 0px;
  color: #5a5a5a;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  letter-spacing: 0px;
  text-align: left;
}

footer {
  font-family: "Montserrat", sans-serif;
  font-weight: 110;
}

body {
  color: red;
}